import { request } from 'utils';
import qs from 'qs';

const prefix = '/api/education';

export const getClassCreditForSelect = ({
  facultyId,
  registerSessionId,
  subjectId,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  request({
    prefix,
    url: '/ClassCreditSchedule/DataForSelect',
    method: 'GET',
    params: {
      facultyId,
      registerSessionId,
      subjectId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled
    },
    data: {}
  });


export const getStudentInClassCredit = ({
  id,
  gradeParts,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  request({
    prefix,
    url: '/ClassCreditSchedule/StudentInClassCredit',
    method: 'GET',
    params: {
      id,
      gradeParts,
      page,
      pageSize,
      orderBy,
      isPagingEnabled
    },
    paramsSerializer: (params) => qs.stringify(params, {arrayFormat: 'repeat'}),
    data: {}
  });
  


