import React, { Component } from 'react';
import { Modal, Table, Checkbox } from 'antd';
import { getGradePart } from 'services/gradePart';
import './style.scss';

const columns = [
  {
    title: 'Mã',
    dataIndex: 'code',
  },
  {
    title: 'Tên',
    dataIndex: 'name',
  },
  {
    title: 'Hệ số',
    dataIndex: 'multiplyBy',
  },
  {
    title: 'Nhóm',
    dataIndex: 'partGroup',
  },
  {
    title: 'Tỷ lệ nhóm',
    dataIndex: 'ratio',
  },
  {
    title: 'Chuyên cần',
    dataIndex: 'isAttendant',
    render: isAttendant => <Checkbox checked={isAttendant} disabled />
  },
  {
    title: 'Mặc định',
    dataIndex: 'isDefault',
    render: isDefault => <Checkbox checked={isDefault} disabled />
  },
];

class GradePartPopup extends Component {
  state = {
    selectedRowKeys: [],
    selectedRowIds: [],
    gradeParts: [],
  };

  async componentWillMount() {
    const result = await getGradePart();
    if(result.data && result.data.items) {
      this.setState({
        gradeParts: result.data.items
      });
    }
  }

  handleCancel = () => {
    this.props.toggleGradePartPopup(false);
  }

  handleOk = () => {
    this.props.toggleGradePartPopup(false);
    this.props.setSelectedGradeParts(this.state.selectedRowIds);
  }

  render() {
    const { selectedRowKeys, gradeParts } = this.state;
    const { visible } = this.props;
    const rowSelection = {
      selectedRowKeys,
      onChange: (selectedKeys, selectedRows) => {
        this.setState({ selectedRowKeys: selectedKeys, selectedRowIds: selectedRows.map(row => row.id) });
      },
    };
    return (
      <Modal
        title="Danh sách điểm thành phần"
        className="popup-grade-part"
        visible={visible}
        onOk={this.handleOk}
        okText="Chọn"
        onCancel={this.handleCancel}
        cancelText="Đóng"
      >
        <Table
          rowSelection={rowSelection}
          columns={columns}
          dataSource={gradeParts}
          pagination={false}
        />
      </Modal>
    );
  }
}

export default GradePartPopup;



