import React, { Component } from 'react';
import { Checkbox, Switch, Form, InputNumber, message, Tooltip } from 'antd';
import { isEmpty } from 'lodash';
// import { BtnDelete } from 'components/common/button';
import { TableDataEditCell } from 'components/common';
import { getStudentInClassCredit } from 'services/classCreditSchedule';
import { lockOrUnlockGradePartGrading } from 'services/gradePartGrading';

class TableEnteringProcessScore extends Component {
  constructor(props) {
    super(props);
    this.state = {
      editedGradePartCredits: {},
      selectedRowKeys: [],
    };
  }

  onSelectChange = (selectedRowKeys, selectedRows) => {
    const { editedGradePartCredits } = this.state;
    const savingEntites = Object.entries(editedGradePartCredits)
      .filter(keyValue => selectedRowKeys.includes(keyValue[0]))
      .map(keyValue => keyValue[1]);
    this.setState({ selectedRowKeys });
    this.props.trackSavingRecord(savingEntites);
    this.props.trackSelectedRecord(selectedRows);
    this.props.toggleDisableActionBtn(isEmpty(selectedRowKeys))
  };

  parseGradePartTableData = rawData => (
    rawData.map(item => {
      item.gradeParts.forEach((part) => {
        item[`gradePart_${part.gradePartId}`] = part.grade
        item.key = this.genGradePartKey(item)
      })
      return item;
    })
  )

  handleSaveCellData = async (rowData) => {
    this.setState({
      editedGradePartCredits: {...this.state.editedGradePartCredits, [this.genGradePartKey(rowData)]: rowData }
    });
  };

  genGradePartKey = row => `${row.classCreditId}_${row.studentId}`;

  handleLockOrUnlock = async data => {
    try {
      await lockOrUnlockGradePartGrading(
        [
          {
            classCreditId: data.classCreditId,
            studentId: data.studentId,
            isLocked: !data.isLocked
          }
        ]
      );
      message.success('Lưu thành công');
      this.props.reloadTableData();
    } catch(err) {
      message.error('Có lỗi xãy ra');
      this.props.reloadTableData();
    }
    
  }

  renderChildRow = () => {
    const { data } = this.state;
    if(!isEmpty(data)) {
      const { gradeParts } = data[0];
      return gradeParts.map((part, index) => (
        {
          title: part.gradePartLabel,
          dataIndex: `gradePart_${index + 1}`,
          editable: true,
          alwayShowEdit: true,
          width: '100px',
          renderFormItemEdit: ({ form, record, onSave }) => (
            <Form.Item>
              {form.getFieldDecorator(`${record.key}.gradePart_${part.gradePartId}`, {
                initialValue: record[`gradePart_${part.gradePartId}`],
              })(
                <InputNumber
                  onPressEnter={onSave}
                  onBlur={onSave}
                  disabled={record.isLocked}
                  max={10}
                  min={0}
                />
              )}
            </Form.Item>
          ),
        }
      ))
    }
    return [];
  }

  render() {
    const columns = [
      {
        title: 'Họ và tên',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Tên lớp',
        dataIndex: 'className',
        key: 'className',
      },
      {
        title: 'Số tiết nghỉ',
        dataIndex: 'noOfLeave',
        key: 'noOfLeave',
        width: '100px',
        align: 'center',
      },
      {
        title: 'Điểm thành phần',
        editable: true,
        width: '100px',
        children: this.renderChildRow()
      },
      {
        title: 'Điểm chuyển / miễn chuyển',
        dataIndex: 'isTransmit',
        width: '200px',
        align: 'center',
        editable: true,
        alwayShowEdit: true,
        renderFormItemEdit: ({ form, record, onSave }) => (
          <Form.Item>
            {form.getFieldDecorator(`${record.key}.isTransmit`, {
              valuePropName: 'checked',
              initialValue: record.isTransmit,
            })(
              // <InputNumber
              //   onPressEnter={onSave}
              //   onBlur={onSave}
              //   disabled={record.isLocked}
              //   max={10}
              //   min={0}
              // />
              <Checkbox
                // checked={record.isTransmit}
                onPressEnter={onSave}
                onBlur={onSave}
                disabled={record.isLocked}
              />
            )}
          </Form.Item>
        ),
        // render: isTransmit => <Checkbox checked={isTransmit} />
      },
      {
        title: 'Thao tác',
        dataIndex: 'isLocked',
        render: (value, record) => (
          <div style={{ textAlign: 'center' }}>
            <Tooltip placement="top" title={value ? 'Mở khóa' : 'Khóa'}>
              <Switch defaultChecked={!value} checked={!value} onChange={() => this.handleLockOrUnlock(record)} />
            </Tooltip>
          </div>
        ),
      },
    ];
    const { selectedRowKeys } = this.state;
    const { paramSearch, reload } = this.props;
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
    };
    return (
      <div>
        <TableDataEditCell
          reload={reload}
          paramSearch={paramSearch}
          service={async params => {
            this.setState({ selectedRowKeys: [] });
            this.props.toggleDisableActionBtn(true);
            const result = await getStudentInClassCredit(params);
            if(result.data && result.data.items) {
              this.setState({ data: this.parseGradePartTableData(result.data.items) });
              this.props.toggleDisableExportExcelBtn(isEmpty(result.data.items));
            }
            return result;
          }}
          columns={columns}
          handleSaveCellData={this.handleSaveCellData}
          rowSelection={rowSelection}
          rowKey="key"
        />
      </div>
    );
  }
}

export default TableEnteringProcessScore;



